.trapezoid {
    border-bottom: 40px solid rgb(33, 10, 10);
    border-left: 15px solid transparent;
    border-right: 15px solid transparent;
    height: 0;
    width: 140px;
    transform: rotate(90deg);
    position: absolute;
    top: 50px;
    left: 460px;
    cursor: pointer;
}
